.root {
    height: 100%;
    border-radius: var(--border-radius-l);
    position: relative;
}

.overflow {
    overflow: auto;
    height: 100%;
    width: 100%;
}

.bg {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    background: var(--bg-placeholder);
    z-index: -1;

   &:before {
        content: "";
        opacity: .1;
        background-image: url("@/icons/bg.svg");
        background-position: top left;
        background-size: 650px;
        background-repeat: repeat;
        position: absolute;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0;
    }
}

.items {
    display: flex;
    /*flex-direction: column;*/
    flex-direction: column-reverse;
    min-height: 100%;
    padding: 0 var(--gap-m);

}

.items > *:first-child {
    margin-top: auto;
}

.from, .to {
    max-width: 85%;
    width: fit-content;
    display: flex;
    align-items: flex-end;
    margin-top: var(--gap-xs);
    position: relative;
}

.from {
    margin-left: auto;
}

.body {
    position: relative;
    display: inline-flex;
    flex-direction: column;
    border-radius: var(--border-radius-l);
    color: var(--text-secondary);

    background: var(--bg-secondary);
    position: relative;
    overflow: hidden;

    min-width: 3em;

    padding: var(--gap-s);
    padding-top: var(--gap-xs);
    padding-bottom: var(--gap-m);

    &:not(:last-child){
         margin-bottom: var(--gap-s);
     }
}

.from .body {
    border-bottom-right-radius: 0;
    background: var(--bg);

    &:before {
        content: '';
        position: absolute;
        height: 100%;
        width: 100%;
        background: var(--custom-color);
        left: 0;
        top: 0;
    }
}

.to .body {
    border-bottom-left-radius: 0;
}

.user {
    position: relative;
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    background: var(--bg-secondary);
    margin-right: var(--gap-xs);
}

.online {
    background: var(--custom-color);
    border: 2px solid var(--bg);
    position: absolute;
    right: 0;
    bottom: 0;
    height: 20%;
    width: 20%;
    border-radius: 50%;
}

.userName {
    font-weight: 600;
    /*width: 100%;*/
}

.time {
    position: absolute;
    font-size: .6em;
    right: var(--gap-xs);
    bottom: var(--gap-xs);
    line-height: 1;
    opacity: .5;
}