.root {
    position: sticky;
    top: 0;
    background: linear-gradient(0deg, var(--bg-0) 0%, var(--bg) 100%);
    padding: var(--gap-m);
    z-index: 2;
    font-weight: 600;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.head {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    font-weight: 600;
}

.desc {
    font-size: 12px;
    color: var(--text-secondary);
}

.btn {
    border: none;
    background: none;
    padding: 0;
    height: 2em;
    width: 2em;
    display: flex;
    justify-content: center;
    align-items: center;
}