.root {
    padding: var(--gap-m);
    background: var(--bg);
    /*border-radius: var(--border-radius-l);*/
    overflow: auto;
    height: 100%;
    z-index: 10;

    max-width: 320px;
    flex-direction: column;

    @media (max-width: 720px) {
        max-width: 100%;
    }
}

.header {
    width: 100%;
    text-align: center;
    font-weight: 600;
    margin-bottom: var(--gap-m);
}

