@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600&display=swap');

:root {
    --gap-xxs: 4px;
    --gap-xs: 8px;
    --gap-s: 12px;
    --gap-m: 16px;

    --border-radius-s: 8px;
    --border-radius-l: 16px;

    --text: #333;
    --text-secondary: #666;

    --border-color: #f7f7f7;

    --bg: #fff;
    --bg-0: #ffffff00;
    --bg-secondary: #eee;
    --bg-placeholder: #fefefe;

    --custom-color: #0054ff2b;
}

.voice {
    height: 20px;
    width: 20px;
}

body {
    font-family: 'Nunito Sans', sans-serif;
    margin: 0;
    padding: 0;
    color: var(--text);
    background: var(--bg-secondary);
}

a {
    text-decoration: none;
    color: inherit;
}

* {
    box-sizing: border-box;

    & *::-webkit-scrollbar {
          width: 14px;
          height: 10px;
      }

    /* Track */
    & *::-webkit-scrollbar-track {
          background: transparent;
      }

    /* Handle */
    & *::-webkit-scrollbar-thumb {
          border: 4px solid rgba(0, 0, 0, 0);
          border-radius: 100px;
          background-color: #d9d9d9;
          background-clip: padding-box;

    &:hover {
         background-color: #999;
     }
    }
}

button {
    font-family: inherit;
    font-size: inherit;
    background: none;
    border: none;
    padding: 0;
}

.root {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding-bottom: 0;
}

.head {
    display: flex;
    justify-content: space-between;
    padding: var(--gap-m);
    font-size: 18px;
}

.title {
    text-align: center;
    width: 100%;
    font-size: 2em;
    margin: 0;
}

.current {
    flex-grow: 1;
    display: flex;
    overflow: hidden;
    position: relative;
}

.current > * {
    width: 100%;
    /*margin: 0 var(--gap-xs);*/
    border: 1px solid var(--border-color);

    @media (max-width: 720px) {
        position: absolute;
    }
}

.hidden {
    display: flex;

    @media (max-width: 720px) {
        display: none;
    }
}

.btn {
    color: var(--text-secondary);
}

.btnSel {
    color: var(--custom-color);
}